:root {
  --Neutral2: #f6f6f8;
  --Neutral8: #838691;
  --Neutral3: #ededf0;
}

.tableview {
  display: flex;
  border-bottom: solid var(--Neutral3) 1px;
  color: var(--Neutral8);
  text-decoration: none;
  font-size: 14px;
  align-items: center;
}

.tableview-device-img {
  height: 25px;
  width: 25px;
  padding-left: 130px;
}
.tableview-device {
  width: 185px;
}

.tableview-device-line {
  width: 260px;
}

.tableview-device-name {
  width: 300px;
}

a:link {
  text-decoration: none;
}

@media screen and (max-width: 600px) {
  .tableview-device-img {
    margin-left: 16px;
    padding-left: 0;
  }
  .tableview-device-line {
    width: 100px;
    margin-right: 20px;
  }
  .tableview-device-name {
    width: 100px;
  }
  .tableview-device {
    width: 80px;
  }
}
