:root {
  --Neutral2: #f6f6f8;
  --Neutral8: #838691;

  --Neutral3: #ededf0;
}

.toolbar {
  border-bottom: solid var(--Neutral3) 1px;
  position: relative;
  z-index: 1;
  display: flex;
}

.search {
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 30px;
}

.search-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--Neutral2);
  padding: 0 8px;
  border-radius: 4px;
  box-shadow: 0 5px 4px -4px rgba(0, 0, 0, 0.2);
}

.search-input {
  border: none;
  background-color: transparent;
  padding: 8px 5px 7px 5px;
  width: 270px;
  outline: none;
}

.search-icon {
  color: var(--Neutral8);
  margin-top: 5px;
}

.search-clear {
  color: var(--Neutral8);
  margin-top: 3px;
  cursor: pointer;
}

.toolbar-list-icon {
  color: var(--Neutral8);
  cursor: pointer;
  padding: 10px;
}

.toolbar-grid-icon {
  color: var(--Neutral8);
  cursor: pointer;
  padding: 10px;
}

.toolbar-filter-text {
  color: var(--Neutral8);
  font-weight: 400;
  font-size: 14px;
  margin-left: 14px;
  margin-right: 32px;
  position: relative;
}

svg.MuiSvgIcon-root {
  font-size: 1rem;
}

svg.MuiSvgIcon-root :hover {
  color: #006fff;
}

.toolbar-filter-popup {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 200px;
  font-size: 14px;
  display: block;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  color: var(--Neutral8);
  padding-bottom: 16px;
}

.toolbar-filter-popup-filter {
  padding-right: 100px;
  color: var(--Neutral8);
  font-weight: 400;
  font-size: 14px;
  border-bottom: solid var(--Neutral3) 1px;
  padding: 0 15px 14px 15px;
}

.toolbar-filter-popup-productline {
  color: var(--Neutral8);
  font-weight: bold;
  font-size: 14px;
  padding: 2px 15px;
}

.toolbar-filter-popup-x {
  position: absolute;
  right: 16px;
}

.toolbar-toggle {
  display: flex;
  position: relative;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .search-input {
    width: 100px;
  }

  .search {
    padding-left: 8px;
  }

  .toolbar-filter-text {
    margin-right: 16px;
  }

  .toolbar-filter-popup {
    right: 0;
  }
  .toolbar-filter-popup-x {
    right: 8px;
  }
}
